import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Button, Stack } from '@mui/material';
import ChatRoom from '../models/ChatRoom';
import ChatRoomComponent from '../common/components/ChatRoomComponent';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { Status } from '../models/User';
import CreateChatRoomComponent from '../common/components/CreateChatRoomComponent';
import { useDispatch, useSelector } from 'react-redux';
import { AppState, setCurrentChatRoom } from '../redux/store';


function ChatRoomView() {
    const [activeRoom, setActiveRoom] = useState<number>(0);
    const [showCreateChatRoom, setShowCreateChatRoom] = useState<boolean>(false);
    const dispatch = useDispatch();
    const chatRooms = useSelector((state: AppState) => state.chatRooms.chatRooms);

    useEffect(() => {
        if (activeRoom === 0) {
            setActiveRoom(123);
        }
    }, [])

    const handleCreateChatRoomClick = () => {
        setShowCreateChatRoom(true);
    }

    const handleChatRoomClick = (chatRoomDetails: ChatRoom) => {
        setActiveRoom(chatRoomDetails.roomId);
        console.log(chatRoomDetails)
        dispatch(setCurrentChatRoom({
            chatRoom:{
                ...chatRoomDetails
            }}))
    }
    return (
        <Stack sx={{ m: '0' }}>
            <Button variant="contained" color='info' endIcon={<AddCircleIcon />} sx={{ m: '4px' }} onClick={handleCreateChatRoomClick}>
                Create Chat Room
            </Button>
            {chatRooms?.map((chatRoom) => {
                return <ChatRoomComponent isActive={chatRoom.roomId === activeRoom} onClick={() => { handleChatRoomClick(chatRoom) }} key={chatRoom.roomId} {...chatRoom} />
            })}
            <CreateChatRoomComponent onCloseDialog={() => { setShowCreateChatRoom(false) }} isOpen={showCreateChatRoom} />
        </Stack>
    );
}

export default ChatRoomView;
