import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import AccountCircle from '@mui/icons-material/AccountCircle';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import LoginRegisterComponent from './LoginRegisterComponent';

export default function MenuAppBar() {
    const [auth, setAuth] = React.useState(true);
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const [showLoginRegisterDialog, setShowLoginRegisterDialog] = React.useState(false);
    const [dialogType, setDialogType] = React.useState('');


    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setAuth(event.target.checked);
    };

    const handleMenu = (event: React.MouseEvent<HTMLElement>) => {
        setAnchorEl(event.currentTarget);
    };

    const handleLogout = () => {

    }
    const handleMenuAction = (type: string) => {
        setShowLoginRegisterDialog(true);
        setDialogType(type);
    }

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <AppBar position="static">
            <Toolbar>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                    Realtime Chat Bot
                </Typography>
                {auth && (
                    <div>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={handleMenu}
                            color="inherit"
                        >
                            <AccountCircle />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={anchorEl}
                            anchorOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'right',
                            }}
                            open={Boolean(anchorEl)}
                            onClose={handleClose}
                        >
                            {!auth && <MenuItem onClick={handleLogout}>Logout</MenuItem>}
                            {auth && <MenuItem onClick={() => handleMenuAction('Login')}>Login</MenuItem>}
                            {auth && <MenuItem onClick={() => handleMenuAction('Register')}>Register</MenuItem>}
                        </Menu>
                    </div>
                )}
            </Toolbar>
            <LoginRegisterComponent isOpen={showLoginRegisterDialog} dialogType={dialogType} onCloseDialog={() =>{ setShowLoginRegisterDialog(false)}} />
        </AppBar>
    );
}
