import React, { useState, useEffect } from 'react';
import styles from './App.module.scss'; // Import the SCSS module
import axios from 'axios';
import User from './models/User';
import config from './config';
import MenuAppBar from './common/components/MenuAppBar';
import ChatView from './views/ChatView';
import { Grid, Stack } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import store, { AppState, setChatRooms, setCurrentChatRoom, setUsers } from './redux/store';

function App() {


  const [isLoading, setIsLoading] = useState<boolean>(false);
  const users = useSelector((state: AppState) => state.users || []);
  const chatRooms = useSelector((state: AppState) => state.chatRooms || []);
  const currentChatRoom = useSelector((state: AppState) => state.currentChatRoom?.chatRoom || null);
  const dispatch = useDispatch();

  const fetchUsers = async () => {
    // Fetch users calling API endpoint
    await axios.get(`${config.serviceUrl}/users`)
      .then(response => {
        if (response?.data.length > 0) {
          dispatch(setUsers(response.data))
        }
        setIsLoading(false);
      })
      .catch(error => {
        console.error('Error fetching users data:', error);
        setIsLoading(false);
      });
  }

  const fetchChatRooms = async () => {
    // Fetch users calling API endpoint
    await axios.get(`${config.serviceUrl}/chatRooms`)
      .then(response => {
        if (response?.data) {
          dispatch(setChatRooms(response.data))
          if (currentChatRoom === null) {
            dispatch(setCurrentChatRoom({ chatRoom: { ...response.data[0] } }))
          }

        }
      })
      .catch(error => {
        console.error('Error fetching users data:', error);
        setIsLoading(false);
      });
  }

  useEffect(() => {
    fetchUsers();
    fetchChatRooms();
  }, []);

  useEffect(() => {
    console.log(users)
  }, [users]);

  useEffect(() => {
    console.log(chatRooms);
  }, [chatRooms]);

  return (
    <Grid container alignItems={'stretch'} sx={{ display: 'flex', flexFlow: 'column', height: '98vh' }}>
      <Grid item>
        <MenuAppBar />
      </Grid>
      <ChatView />
    </Grid>
  );
}

export default App;
